import Scroll from 'base/scroll/scroll'
import { writeorder , submitCardOrder, cardPay} from 'api/api'
import { mapGetters } from 'vuex'
export default {
  data() {
    return {
      data: [],
      totalmoney: '',
      addressid: '',
      cartList: []
    }
  },
  created() {
    this._getWriteOrder()
  },
  computed: {
    ...mapGetters([
      'userAccount',
      'address'
    ])
  },
  methods: {
    back() {
      this.$router.back()
    },
    toAddress() {
      this.$router.push({
        path: '/address'
      })
    },
    submit() {
      // 提交购物车订单
      let addressid = this.addressid
      let uid = this.userAccount.uid
      let token = this.userAccount.token
      let couponid = 0
      submitCardOrder(addressid,uid,token,couponid).then((res)=>{
        // console.log(res)
        if(res.status === 1) {
          let recordid = res.data.cardid
          let pay_type = 'weixin'
          let order_type = 'cardpay'
          return cardPay(uid,token,recordid,pay_type,order_type)
        }else{
          alert('接口报错，不能调起支付')
        }
      }).then((res)=>{
        // console.log(res)
        // 进行微信支付相关操作
        // alert(JSON.stringify(res))
        let data = JSON.parse(res.errorMsg)

        // alert(data)
        let appid = data.appId
        let timestamp = data.timeStamp
        let nonceStr = data.nonceStr
        let packageFuck = data.package
        let signType = 'MD5'
        let paySign = data.paySign
        // alert(appid)
        WeixinJSBridge.invoke(
          'getBrandWCPayRequest', {
              "appId": appid,     //公众号名称，由商户传入     
              "timeStamp": timestamp,         //时间戳，自1970年以来的秒数     
              "nonceStr": nonceStr, //随机串     
              "package": packageFuck,     
              "signType": signType, //微信签名方式     
              "paySign": paySign //微信签名 
          },
          function(res){ 
              // alert(JSON.stringify(res))   
              if(res.err_msg == "get_brand_wcpay_request:ok" ) {

              }     // 使用以上方式判断前端返回,微信团队郑重提示：res.err_msg将在用户支付成功后返回    ok，但并不保证它绝对可靠。 
          }
        )
      })
    },
    _getWriteOrder() {
      let token = this.userAccount.token
      
      let uid = this.userAccount.uid
      // let addressid = '啦啦啦'
      writeorder(token,uid).then((res)=>{
        console.log(res)
        this.addressid = res.data.adressinfo.adress_id
        this.cartList = res.data.cards
        console.log(this.cartList)
        this.totalmoney = res.data.total_money
      })
    }
  },
  components: {
    Scroll
  }
}



